import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {Observable} from "rxjs";
import {HttpClient} from "@angular/common/http";
import {TokenStorage} from "@app/features/auth/login/token.storage";
import {loginService} from "@app/features/auth/login/login.service";

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  providers: [loginService]
})
export class LoginComponent implements OnInit {
  password: string;
  username: string;

  constructor(private router: Router,private http: HttpClient,private token: TokenStorage,private loginservice:loginService) { }

  ngOnInit() {
  }


  login() {
    this.loginservice.attemptAuth(this.username, this.password).subscribe(
        data => {
          this.token.saveToken(data.token);
         console.log("works");
        }
    );
  }



   // login(event){
    // event.preventDefault();
    //



}
